@import url(https://fonts.googleapis.com/css?family=Open+Sans|Raleway:400,500&display=swap);

:root,
* {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-image: linear-gradient(-137deg, #adbcc4 0%, #696d89 100%);
  font-family: "Raleway", sans-serif;
  font-size: 1.5rem;
  color: #dcdeed; }

@media (max-width: 499px) {
  .App {
    height: 100%;
    width: 100vw;
    border-radius: 0; }
  body {
    background: #34394c;
    touch-action: manipulation; } }

.gear {
  position: fixed;
  top: 0;
  right: 0;
  padding: 20px;
  -webkit-transition: -webkit-transform 400ms;
  transition: -webkit-transform 400ms;
  transition: transform 400ms;
  transition: transform 400ms, -webkit-transform 400ms;
  font-size: 2rem;
  z-index: 4;
  color: #34394c; }
  .gear:hover {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }

.pomodoro {
  width: 350px;
  height: 600px;
  border-radius: 6px;
  background: #34394c;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
  -webkit-transition: background 1s, color 1s;
  transition: background 1s, color 1s; }
  .pomodoro-light {
    background: #f2f4f1;
    color: #34394c; }

@media (max-width: 499px) {
  .pomodoro {
    height: 100%;
    width: 100vw;
    border-radius: 0; }
  .gear {
    color: #f2f4f1; } }

.title {
  /* Pomodoro Timer: */
  font-weight: 500;
  font-size: 1.5rem;
  letter-spacing: 2.25px;
  text-align: center; }

.wrapper {
  position: relative;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center; }

.counter {
  position: absolute;
  z-index: 2;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.spinner {
  z-index: 1;
  width: 100%;
  height: 100%; }

.counterType {
  font-size: 1rem; }

#time-left {
  font-family: "Open Sans", sans-serif; }

.dot {
  height: 10px;
  width: 10px;
  background-color: #dcdeed;
  border-radius: 50%;
  display: inline-block;
  margin: 25px 10px; }

.completed {
  background-color: #f28458; }

.TimeController {
  display: flex;
  justify-content: space-around;
  width: 80%;
  padding: 1rem 0; }

.wrapperDisplay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.label {
  font-size: 0.7rem;
  color: #f28458;
  font-size: 1.125; }

.time {
  display: inline-block;
  font-size: 1.5rem; }
  .time::after {
    content: " mins"; }

.controlButton {
  background: #dcdeed;
  text-align: center;
  width: 3rem;
  height: 3rem;
  border-radius: 100%;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: #34394c;
  font-weight: bold;
  text-decoration: none;
  border: none; }

.button {
  background: #f2f4f1;
  color: #34394c;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  text-decoration: none;
  -webkit-transition: color 1s, background 1s;
  transition: color 1s, background 1s; }
  .button:hover {
    cursor: pointer; }
  .button:focus {
    outline: 0; }
  .button-light {
    background: #f28458;
    color: #f2f4f1; }

.content {
  position: absolute;
  font-size: 1.5rem;
  font-weight: bold;
  position: absolute;
  z-index: 2;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.ButtonController {
  display: flex;
  justify-content: space-around;
  width: 80%;
  margin: 1rem 0 2rem 0; }

.modal-show {
  position: fixed;
  /* Stay in place */
  z-index: 3;
  /* Sit on top */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */ }

.modal-hide {
  display: none; }

/* Modal Content */
.modal-content {
  position: relative;
  background-color: #f2f4f1;
  color: #34394c;
  margin: auto;
  padding: 0;
  width: 60%;
  box-shadow: 0 4px 8px 0 rgba(52, 57, 76, 0.2), 0 6px 20px 0 rgba(52, 57, 76, 0.19);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s; }

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0; }
  to {
    top: 0;
    opacity: 1; } }

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0; }
  to {
    top: 0;
    opacity: 1; } }

/* The Close Button */
.close {
  color: #f2f4f1;
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 28px;
  font-weight: bold;
  width: 30px;
  height: 30px;
  text-align: center; }

.close:hover,
.close:focus {
  color: #f2f4f1;
  text-decoration: none;
  cursor: pointer; }

.modal-header {
  padding: 2px 16px;
  background-color: #34394c;
  color: #f2f4f1; }

.modal-body {
  padding: 2px 16px;
  display: flex;
  justify-content: center; }

.modal-footer {
  padding: 2px 16px;
  background-color: #34394c;
  color: #f2f4f1; }

.modal__container {
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center; }

input[type="checkbox"] {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  position: relative;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 8rem;
  height: calc(8rem / 2);
  background-color: #f2f4f1;
  border-radius: calc(8rem / 2);
  outline: none;
  -webkit-transition: background 450ms ease;
  transition: background 450ms ease;
  box-shadow: 0 0 0 3px #dee3dc; }
  input[type="checkbox"]:before, input[type="checkbox"]:after {
    position: absolute;
    display: block;
    content: "";
    border-radius: 100%;
    -webkit-transition: background 450ms ease, -webkit-transform 450ms ease;
    transition: background 450ms ease, -webkit-transform 450ms ease;
    transition: background 450ms ease, transform 450ms ease;
    transition: background 450ms ease, transform 450ms ease, -webkit-transform 450ms ease; }
  input[type="checkbox"]:before {
    width: calc(8rem / 2);
    height: calc(8rem / 2);
    background-color: #34394c; }
  input[type="checkbox"]:checked:before {
    background-color: #f28458;
    -webkit-transform: translateX(100%);
            transform: translateX(100%); }

